import React from 'react'
import Logo from '../assets/img/logo.png'
import food1 from '../assets/img/food1.png'
import food2 from '../assets/img/food2.png'
import ok from '../assets/img/ok.svg'
import Notice from './Notice'
import Product from './Product'
import Buy from './Buy'

class Home extends React.Component{
    constructor(){
        super()
        this.state={
          noticeAction:'close',
          myCartAction:'close',
          data:[
              {id:1,name:'甜糰子',img:food1,price:'110',text:'日本甜糰子，好吃不膩口，代表春夏冬，三個顏色'},
              {id:2,name:'熱湯粥',img:food2,price:'150',text:'熱湯粥，料多豐富，鮮美湯頭，暖意泉湧'}
          ],
          buy:[],
          total:'',
          fare: 100,
          num:1
        }
    }

    handleMyCartClose=()=>{
        this.setState({myCartAction:'close'})
     }
    handleMyCartOpen=()=>{
       this.setState({myCartAction:'open'})
    }
    handleAddProduct=(e)=>{
        // console.log(e.target)
        
        let id = e.target.parentNode.parentNode.getAttribute('data-id')
        // console.log(this.state.data[+id-1])
        // console.log(this.state.buy.indexOf(id))
        if(this.state.buy.indexOf(id) === -1){
            this.setState({buy:[...this.state.buy,id]},
                ()=>{console.log(this.state.buy)})
        }else{
             console.log(this.state.myCartAction)
        }
        
        
        // console.log(this.state.buy)
        if(this.state.myCartAction==='close'){
            this.handleMyCartOpen()
        }   
    }
    handleNotice=()=>{
        if(this.state.noticeAction==='active'){
          this.setState({noticeAction:'close'})
        }else{
          this.setState({noticeAction:'active'}) 
        }  
    }
    total=()=>{
      return parseInt(this.state.fare)
    }
    handleAdd=(vaule)=>{
        if(this.state.num<1){
            this.setState({num:this.state.num})
        }else{
            this.setState({num:this.state.num+vaule})
        }
        
        console.log('nnnnnn')
    }
    counter=()=>{
    }
    render(){
        return(
            <>
              <div id="wrap">
                <header>
                    <img src={Logo} alt="logo" id="logo"/>
                    <div className="myCart pointer" onClick={this.handleMyCartOpen}>B</div>
                </header>
                <section>
                    <div className="goods">
                    {this.state.data.map((vaule,index)=>{
                        return(
                            <Product
                               key={vaule.id}
                               id={vaule.id}
                               name={vaule.name}
                               img={vaule.img}
                               price={vaule.price}
                               text={vaule.text}
                               onClick={this.handleAddProduct} 
                            />                 
                            )
                    })}
                        
                        {/* <div className="product" key={vaule.id}>
                            <h2>-{vaule.name}-</h2>
                            <img src={vaule.img} alt=""/>
                            <div className="info">
                                <div>NT{vaule.price}</div>
                                <div className="pointer">B</div>
                            </div>
                            <p>{vaule.text}</p>
                            </div> */}
                       
                       {/* <div className="product">
                            <h2>-甜糰子-</h2>
                            <img src={food1} alt=""/>
                            <div className="info">
                                <div>NT120</div>
                                <div className="pointer">B</div>
                            </div>
                            <p>日本甜糰子，好吃不膩口，
                               代表春夏冬，三個顏色</p>
                       </div>
                       <div className="product">
                            <h2>-甜糰子-</h2>
                            <img src={food1} alt=""/>
                            <div className="info">
                                <div>NT120</div>
                                <div className="pointer" onClick={this.handleAddProduct}>B</div>
                            </div>
                            <p>日本甜糰子，好吃不膩口，代表春夏冬，三個顏色</p>
                        </div>                                   */}
                    </div>
                </section>
                <aside className={(this.state.myCartAction==='open')?'':'cartHide'}>
                  <div className="cartPart1">
                      <span>你的購物車</span>
                      <span className="pointer" onClick={this.handleMyCartClose}>X</span>
                  </div>
                  <div className="cartPart2">
                      <div className="buyList">
                         {}
                         
                         {(this.state.buy.length === 0)?'': this.state.buy.map((vaule,index)=>{return <Buy key={vaule} id={vaule-1} name={this.state.data[vaule-1].name} price={this.state.data[vaule-1].price} img={this.state.data[vaule-1].img} num={this.state.num} onClick={this.handleAdd} counter={this.counter()}/>})}
                         {/* {(this.state.buyId)?<Buy id={this.state.buyId}/>:''} */}

                         {/* <div className="buy">
                            <div className="buyPart1">
                                <img src={food1} alt=""/>
                            </div>
                            <div className="buyPart2">
                                <span>-甜糰子-</span>
                                <i>NT120</i>
                                <div className="quantity">
                                  <span className="pointer">-</span>
                                  <i>1</i>
                                  <span className="pointer">+</span>
                                </div>
                            </div>
                            <div className="buyPart3 pointer">X</div>
                         </div> */}

                         {/* <Buy /> */}

                         {/* <div className="buy">
                            <div className="buyPart1">
                                <img src={food1} alt=""/>
                            </div>
                            <div className="buyPart2">
                                <span>-甜糰子-</span>
                                <i>NT120</i>
                                <div className="quantity">
                                  <span className="pointer">-</span>
                                  <i>1</i>
                                  <span className="pointer">+</span>
                                </div>
                            </div>
                            <div className="buyPart3 pointer">X</div>
                         </div> */}


                      </div>
                      <div className="count">
                        <div className="ps">
                            <p>
                                注意:<br/>
                                若商品有瑕疵，請保存並與公司聯繫。
                            </p>
                            <div>購物滿NT300免運費</div>
                        </div>
                        <div>
                            <div className="spend"><span>運費</span><span>NT{this.state.fare}</span></div>
                            <div className="spend"><span>總計</span><span>NT{this.total()}</span></div>
                        </div>
                      </div>
                  </div>
                  <div className="cartPart3 pointer" onClick={this.handleNotice}>
                      <span>結</span>帳
                  </div>
                </aside>
              
              </div>
              {/* 購買完成通知 */}
              <Notice 
                  noticeAction={this.state.noticeAction}
                  onClick={this.handleNotice}
              />

              {/* <div id="notice" className={(this.state.noticeAction==='active')?'':'hide'}>
                    <div className="noticeCard">
                       <img src={ok} alt="" />
                       <h1>-結帳完成-</h1>
                       <span className="pointer" onClick={this.handleCloseNotice}>確認</span>
                    </div>
              </div> */}
              

            </>
        )
    }
}

export default Home;