import React from 'react'
import food1 from '../assets/img/food1.png'


class Buy extends React.Component{
    constructor(){
        super()
        this.state={
        }
    }
    render(){
        return(
            <>
                <div className="buy">
                    <div className="buyPart1">
                        <img src={this.props.img} alt=""/>
                    </div>
                    <div className="buyPart2">
                        <span>-{this.props.name}-</span>
                        NT<i>{this.props.price}</i>
                        <div className="quantity">
                            <span className="pointer" onClick={(this.props.num === 1 )?()=>{}:()=>this.props.onClick(-1)}>-</span>
                            <i>{this.props.num}</i>
                            <span className="pointer" onClick={()=>this.props.onClick(+1)}>+</span>
                        </div>
                    </div>
                    {this.props.counter}
                    <div className="buyPart3 pointer">X</div>
                </div>
            </>
        )
    }
}

export default Buy;